body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  margin: 0;
}
.Header{
  background-color: black;
  padding-top: 35px;
  color: beige;
  display: -webkit-flex;
  display: flex;
}

.Header_text{
  margin:0;
  padding-bottom: 35px;
  font-size: 60px;
  padding-left:60px;
}
.Search_div{
 margin-left: 43%;
 margin-top: 50px;
}

.Search_button{
  height: 30px;
  width: 100px;
  border-radius: 100px;
  background-color:gold;
  border-block-start-color: blueviolet;
  border-block-end-color: blueviolet;
  margin-right: 5px;
}
.Search_text{
  border-radius: 10px;
  height: 20px;
}


.Home{
  margin-left:100px;
  margin-right:100px;
  margin-top: 50px;

}

.item{
  width: 25%;
  height: 430px;
  margin: 0;
  text-align: center;
  float: left;
  padding-top: 30px;
  margin-left: 5%   !important;
}

.img-size{
  width: 100%;
  height: 350px;
}
.yellow{
  background-color: yellow  !important;
  padding: 10px 0.5px  !important;
  margin-bottom: 0;
  width: 65.21%;  
  margin-left: 17.4%;                                                      

}

.anime-P{
  margin-bottom: 0;
  padding-left: 50px;
}

video {
	width: 60% !important;
  height: auto !important;
  margin-left: 17%;
  margin-top: 10%;
  margin-bottom: 10%;
  border-radius: 15px;
}


.container{
  background-color: grey;
  width:70%;
  padding: 0;
  margin-left: 15%   !important; 
  border-style: solid;
  border-width:0.5px;
  border-block-start-width:0; 
  border-color: gold;

}


.video_image{
  padding: 50px 0;
background-color: blueviolet;
}


.info_image{
padding-left: 15px;
padding-top: 20px;
padding-bottom: 40px;
height: 450px;
}

.info_name{
  font-size: 40px;
  padding-top: 20px;
  padding-left: 30px;
  color: whitesmoke;

}

.flex{
  display: -webkit-flex;
  display: flex;
}

.anime_Links{
  color: aqua;
  padding-left: 20px;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-size: 30px;
}

.welcome-text{
  margin-top: -24%;
  color: #FF7433;
  font-size: 230%;
  margin-left: 17%;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
}

.welcome-img{
  margin-left: -10%;
  width: 80%;

}
.Loading{
  padding-top: 100px;
  padding-left:30%;
  padding-bottom: 350px;
  background-color: black;
}

@-webkit-keyframes example {
  0%   {font-size: 20px;
    margin-left: 270px;
    margin-top: -120px;
  }
  100% {  font-size: 230%;
    margin-left: 17%;
    margin-top: -24%;}
}

@keyframes example {
  0%   {font-size: 20px;
    margin-left: 270px;
    margin-top: -120px;
  }
  100% {  font-size: 230%;
    margin-left: 17%;
    margin-top: -24%;}
}




